:root {
	--secondary-color: #449399;
}

body {
	font-size: 14px;
}

/*
This is the parent `<div>` that contains the menu and the content area.
*/
#layout {
	position: relative;
	left: 0;
	padding-left: 0;
}
/*
The content `<div>` is where all your content goes.
*/
.content {
	margin: 0 auto;
	padding: 0 2em;
	max-width: 800px;
	margin-bottom: 50px;
	line-height: 1.6em;
}

.header {
	margin: 0;
	color: #333;
	/*text-align: center;*/
	/*padding: 2.5em 2em 0;*/
	/*border-bottom: 1px solid #eee;*/
}
.header h1 {
	margin: 0.2em 0;
	font-size: 2.5em;
}
.header h2 {
	color: var(--secondary-color);
	padding: 0;
	margin: 0;
}

.content-subhead {
	margin-block-end: 0;
	color: var(--secondary-color);
	font-size: 15px;
	text-transform: uppercase;
	border-bottom: 1px solid #dadada;
	margin-top: 5px;
}


.grid-container {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1fr;
	gap: 1px 1px;
	grid-template-areas: "name social";
}

.name { grid-area: name; }

.social {
	grid-area: social;
	text-align: end;
}

.profile-items {
	/*float: left;*/
}

.profile-items i {
	margin-left: 5px;
	height: 14px;
	width: 14px;
}

.skills {
	margin: 0;
	padding: 5px;
}

.no-list-style {
	list-style: none;
	margin: 0;
	padding: 0;
}

.chip {
	display: inline-block;
	width: auto;
	font-size: 13px;
	line-height: 18px;
	border-radius: 4px;
	padding: 0 4px;
	margin-right: 3px;
	margin-bottom: 4px;
	background-color: var(--secondary-color);
	color: white;
}

.social > li:nth-child(n+3) {
	float: right;
	margin-right: 10px;
}

.social > li:nth-child(n+3) i {
	font-size: 25px;
}

.float-right {
	display: inline-block;
	float: right;
}

section > ul {
	margin: 0;
	list-style: circle;
}

a {
	color: #337ab7;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

.italics {
	font-style: italic;
}

.time-icon {
	margin-right: 3px;
}
